.NavbarItems{
    display: flex;
    background-color: #fff;
    justify-content: space-between;
    align-items: center;
    padding: 0 30px;
    box-shadow: 0 5px 15px 0 rgba(0,0,0,.25);
    width: 90%;
    height: 80px;
    border-radius: 13px;
    position: fixed;
    top: 20px;
    left: 50%;
    transform: translate(-50%);
    z-index: 9999;
}
.Navbar-logo{
    color: #222;
    font-size: 2rem;
    cursor: pointer;
}
.nav-menu{
    display: grid;
    grid-template-columns: repeat(6,auto);
    grid-gap: 10px;
    list-style: none;
    align-items: center;
}
.nav-links{
   text-decoration: none;
   color: #222;
   font-size: 1.2rem;
   font-weight: 600;
   padding: 0.7rem 1rem;
   white-space: nowrap;
}

.nav-links:hover{
    background-color: #01959a;
    color: #fff;
    border-radius: 4px;
    transition: all 0.2s ease-in-out;

}
.nav-links i {
    padding-right: 10px;
}
button{
    padding: 0.5rem 1rem;
    white-space: nowrap;
    border-radius: 0.3rem;
    font-size: 1rem;
    font-weight: 600;
    border: none;
    cursor: pointer;
}
button:hover{
    background-color: #151516;
    color: #fff;
    transition: 0.2s ease-in-out;
}
.nav-links-mobile{
    display: none;
}
.menu-icons{
    display: none;
}
@media screen and (max-width: 850px) {
    .NavbarItems{
        z-index: 99;
    }
    .nav-menu{
       display: flex;
       flex-direction: column;
       justify-content: flex-start;
        list-style: none;
        align-items: center;
        background: #fff;
        width: 100%;
        height: auto;
        position: absolute;
        top: 0;
        left: -100%;
        opacity: 1;
        align-items: stretch;
        padding: 80px 0 30px 0;
        margin: 0;
        z-index: -1;
        transition: 0.3s ease-in-out;
        border-radius: 13px;
        position: fixed;
        z-index: 99999;
    }
    .nav-menu.active{
        left: 0;
        opacity: 1;
        z-index: -1;
        transition: 0.3s ease-in-out;
    }
    .nav-links{
      display: flex;
      justify-content: center;
      width: 100%;
      font-size: 1.2rem;
      padding: 2rem 0;
     }
     .nav-links:hover{
        background-color: #01959a;
        color: #fff;
     }
    
     button{
    display: none;
       
    }
    .nav-links-mobile{
        display: flex;
        justify-content: center;
        text-align: center;
        padding: 1.5rem;
        margin: auto;
        border-radius: 4px;
        width: 80%;
        background-color: #01959a;
        text-decoration: none;
        color: #fff;
        font-weight: 700;
        letter-spacing: 2px;
    }
    .menu-icons{
        display: block;
        cursor: pointer;
    }
    .menu-icons i {
        font-size: 1.2rem;
        color: #222;
    }
}
